.footer {
    height: 55px;
    background: #0067A3;
    display: flex;
    color: #FFFF;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 20px;
}


.counts {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
}


.count {
    margin-right: 20px;
}


@media only screen and (max-width: 550px) {
    .footerText {
        font-size: 8px;
    }
}

@media only screen and (max-width: 410px) {
    .count {
        font-size: 10px;
    }
}
